.ambassador-list-container {
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 1150px;
  padding: 0 20px;
  width: 100%;
}

.ambassador-list-row {
  margin: 0 -20px;
}

.ambassador-list-row::after {
  clear: both;
  content: '';
  display: block;
  height: 0;
  width: 100%;
}

.ambassador-list-row .ambassador-list-col {
  box-sizing: border-box;
  float: left;
  margin: 20px 0;
  padding: 0 20px;
  width: 25%;
}

@media (max-width: 900px) {
  .ambassador-list-row .ambassador-list-col {
    width: 50%;
  }
}

@media (max-width: 480px) {
  .ambassador-list-row .ambassador-list-col {
    width: 100%;
  }
}

.ambassador-list-thumbnail {
  cursor: pointer;
  position: relative;
}

.ambassador-list-thumbnail-img {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
}

.ambassador-list-thumbnail-caption {
  background-color: #eee;
  padding: 15px;
}

.ambassador-list-thumbnail-caption::after {
  clear: both;
  content: '';
  display: block;
}

.ambassador-list-thumbnail-caption .owner-img {
  float: left;
  margin: 0 15px 0 0;
}

.ambassador-list-thumbnail-caption h3,
.ambassador-list-thumbnail-caption p {
  font-family: 'SST W01 Roman', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  font-size: 14px;
  line-height: 19px;
  margin: 0;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.ambassador-list-thumbnail-caption h3 {
  font-weight: 600;
  margin-top: 12px;
}

.ambassador-list-thumbnail-caption p {
  font-weight: 300;
}

.ambassador-list-thumbnail-url {
  height: 100%;
  left: 0;
  position: absolute;
  top: 0;
  width: 100%;
}

.ambassador-list-thumbnail-url:hover, .ambassador-list-thumbnail-url:focus, .ambassador-list-thumbnail-url:active {
  border: 0;
  text-decoration: none;
}
